<template>
  <div class="main" data-app="true">
    <h1>{{ msg }}</h1>
    <ul>
      <li>
        <a href="#" @click="openCreateNewRoomDialog">Create new room</a>
      </li>
      <li>
        <a href="#" @click="openEnterRoomDialog">Enter StrEAMer</a>
      </li>
      <li>
        <a href="#" @click="openRoomListDialog">Room List</a>
      </li>
    </ul>
    <br>
    <div v-show="showCreateNewRoomDialog">
      <create-new-room @created="openEnterRoomDialog" />
    </div>
    <div v-show="showEnterRoomDialog">
      <enter-room :login-room="room" />
    </div>
    <div v-show="showRoomListDialog">
      <room-list @login="onLogin($event)" />
    </div>
  </div>
</template>

<script>
import EnterRoom from './EnterRoom'
import CreateNewRoom from './CreateNewRoom'
import RoomList from './RoomList'

export default {
  name: 'Main',
  data () {
    return {
      msg: 'Welcome to Chatting StrEAMer Application',
      showEnterRoomDialog: false,
      showCreateNewRoomDialog: false,
      showRoomListDialog: false,
      room: ''
    }
  },
  components: {
    EnterRoom,
    CreateNewRoom,
    RoomList
  },
  methods: {
    openEnterRoomDialog () {
      this.showEnterRoomDialog = !this.showEnterRoomDialog
      this.showCreateNewRoomDialog = false
      this.showRoomListDialog = false
    },
    openCreateNewRoomDialog () {
      this.showCreateNewRoomDialog = !this.showCreateNewRoomDialog
      this.showEnterRoomDialog = false
      this.showRoomListDialog = false
    },
    openRoomListDialog () {
      this.showRoomListDialog = !this.showRoomListDialog
      this.showEnterRoomDialog = false
      this.showCreateNewRoomDialog = false
    },
    onLogin (room) {
      this.room = room
      this.openEnterRoomDialog()
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



