<template>
  <div class="container">
    <v-data-table
      :headers="headers"
      :items="rooms"
      :rows-per-page-items="rows_per_page"
    >
      <template slot="items" slot-scope="props">
        <td><a href="#" @click="login(props.item.id)">{{ props.item.id }}</a></td>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import roomsRef from './../firebase'

export default {
  name: 'RoomList',
  data () {
    return {
      rows_per_page: [5, 10, 25, { "text": "All", "value": -1 }],
      headers: [
        {
          text: 'Room number',
          align: 'center',
          sortable: true,
          value: 'id'
        }
      ]
    }
  },
  firebase: {
    rooms: roomsRef
  },
  methods: {
    login (id) {
      this.$emit('login', id)
    }
  }
}
</script>

<style>
  .menu__content {
    background-color: #fff;
  }
</style>



