<template>
  <v-layout justify-center>
    <v-flex xs10 sm8 md6 lg4>
      <v-card ref="form">
        <v-alert :value="invalid" type="error">
          Room exists
        </v-alert>
        <v-card-text>
          <span>Random a room: <strong>{{ room }}</strong></span>
          <v-text-field
            ref="room"
            v-model="room"
            :rules="[() => !!room || 'This field is required']"
            label="Room"
            placeholder="Enter room name"
            required
          ></v-text-field>
          <v-text-field
            ref="password"
            :rules="[
              () => !!password || 'This field is required',
              () => !!password && password.length >= 6 || 'Address must be greater than 6 characters'
            ]"
            v-model="password"
            label="Password"
            placeholder="Enter password for new room"
            counter="6"
            :append-icon="isPassword ? 'visibility' : 'visibility_off'"
            :append-icon-cb="() => (isPassword = !isPassword)"
            :type="isPassword ? 'password' : 'text'"
            required
            hint="At least 6 characters"
            min="6"
          ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-btn flat @click="room = generateRoomNumber()">Generate Room Number</v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" flat @click="create">Create</v-btn>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import roomsRef from './../firebase'

export default {
  name: 'CreateNewRoom',
  data () {
    return {
      isPassword: true,
      password: null,
      formHasErrors: false,
      room: this.generateRoomNumber(),
      invalid: false
    }
  },
  methods: {
    create () {
      this.formHasErrors = false
      Object.keys(this.form).forEach(f => {
        if (!this.form[f]) this.formHasErrors = true
        this.$refs[f].validate(true)
      })
      if (!this.formHasErrors) {
        if (this.isExist()) {
          this.invalid = true
          return
        }
        let self = this
        roomsRef.push({ id: this.room, password: this.password }, function(error) {
          if (!error) self.$emit('created')
        })
      }
    },
    generateRoomNumber () {
      return Math.random().toString().slice(2, 10)
    },
    isExist () {
      let exist = false
      roomsRef.orderByChild('id').equalTo(this.room).once('value', snapshot => {
        if (snapshot.val()) exist = true
      })
      return exist
    }
  },
  computed: {
    form () {
      return {
        password: this.password
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.error--text {
  color: #ff5252 !important;
}
.error {
    background-color: #ff5252 !important;
}
</style>


