import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Room from '@/components/pages/Room'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path: '/room/:id',
      name: 'Room',
      component: Room,
      props: true
    }
  ]
})



