<template>
  <v-layout justify-center>
    <v-flex xs10 sm8 md6 lg4>
      <v-card ref="form">
        <v-alert :value="invalid" type="error">
          Invalid Room number or Password
        </v-alert>
        <v-card-text>
          <v-text-field
            ref="room"
            v-model="room"
            :rules="[() => !!room || 'This field is required']"
            label="Room Number"
            placeholder="Enter room number"
            required
          ></v-text-field>
          <v-text-field
            ref="password"
            :rules="[
              () => !!password || 'This field is required',
              () => !!password && password.length >= 6 || 'Address must be greater than 6 characters'
            ]"
            v-model="password"
            label="Password"
            placeholder="Enter room password"
            counter="6"
            :append-icon="isPassword ? 'visibility' : 'visibility_off'"
            :append-icon-cb="() => (isPassword = !isPassword)"
            :type="isPassword ? 'password' : 'text'"
            required
            hint="At least 6 characters"
            min="6"
          ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-slide-x-reverse-transition>
            <v-tooltip left >
              <v-btn
                slot="activator"
                icon
                class="my-0"
                @click="resetForm"
              >
                <v-icon>refresh</v-icon>
              </v-btn>
              <span>Refresh form</span>
            </v-tooltip>
          </v-slide-x-reverse-transition>
          <v-btn color="primary" flat @click="enter">Enter</v-btn>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import roomsRef from './../firebase'

export default {
  name: 'EnterRoom',
  data () {
    return {
      isPassword: true,
      room: null,
      password: null,
      formHasErrors: false,
      invalid: false
    }
  },
  props: ['loginRoom'],
  methods: {
    resetForm () {
      this.formHasErrors = false
      Object.keys(this.form).forEach(f => {
        this.$refs[f].reset()
      })
    },
    enter () {
      this.formHasErrors = false
      Object.keys(this.form).forEach(f => {
        if (!this.form[f]) this.formHasErrors = true
        this.$refs[f].validate(true)
      })
      if (!this.formHasErrors) {
        let self = this
        roomsRef.orderByChild('id').equalTo(this.room).once('value', snapshot => {
          snapshot.forEach(function(data) {
            self.checkValid(data.val())
          })
        })
        this.invalid = true
      }
    },
    checkValid (data) {
      if (data.id == this.room && data.password == this.password) this.$router.push({ name: 'Room', params: { id: this.room } })
    }
  },
  computed: {
    form () {
      return {
        room: this.room,
        password: this.password
      }
    }
  },
  watch: {
    loginRoom (val) {
      this.room = val
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.error--text {
  color: #ff5252 !important;
}
.error {
    background-color: #ff5252 !important;
}
</style>


